export const relics = [
  "W czasie potopu Szwedzi splądrowali i zburzyli kościół, a nie mogąc zniszczyć ściany z Cudownym Wizerunkiem, zasypali ją gruzem. Pod gruzami obraz przetrwał do 1657 r., kiedy to Kraków wyzwolono. Obecny klasztor powstał w 2 połowie XVII w. i posiada barokowy wygląd.",
  "Zwiedzającym udostępnione są krużganki i przyległe do nich pomieszczenia oraz w wyznaczone dni roku ogród klasztorny. Kolebkowe sklepienia krużganków pokrywają malowidła pochodzące z połowy XVIII w. – obrazują 21 scen. Scena 22 powstała pod koniec XIX w. i przedstawia koronację wizerunku Matki Bożej Piaskowej – Pani Krakowa – w 1883 r. Ponadto na uwagę zasługuje ołtarz ukształtowany ze splotów suchego akantu, a dedykowany karmelicie, bł. Frankusowi, i Matce Bożej. Do krużganków przylega przestronna klatka schodowa pochodząca z 2 połowy XVII w., prowadząca do klasztoru, przypominająca kompozycją przestrzenną rozwiązania weneckie. Przy podeście schodów znajduje się posąg Madonny z Dzieciątkiem, ustawiony na postumencie opatrzonym datą 1674 r."
];

export const facts = [
  "Obraz Matki Bożej Piaskowej został koronowany 8 września 1883 r., w dwusetną rocznicę odsieczy wiedeńskiej. Korony zostały zaprojektowane przez Jana Matejkę i mają charakter patriotyczny. Motywy do korony Maryi zostały zaczerpnięte z koron królewskich. Lilie nawiązują do korony Bolesława Chrobrego, łuki diademu – do korony Zygmunta Starego. W czasie konfederacji barskiej (1768–1772) wojska rosyjskie usiłowały zdobyć Kraków. Z wdzięczności za ocalenie miasta krakowianie umieścili kopię obrazu Matki Bożej Piaskowej w Bramie Floriańskiej.",
  "Na zewnętrznej ścianie kościoła od ul. Garbarskiej znajduje się kamień, na którym, według podania, utrwalony jest ślad stopy św. Królowej Jadwigi. Legenda mówi, że podczas jednej z wizyt zdjęła zabrudzony trzewik, aby dać go do oczyszczenia. W tym czasie wdała się w rozmowę z jednym z kamieniarzy. Przejęta jego trudną sytuacją życiową ofiarowała mu cenny pierścień. Gdy odeszła, robotnik zauważył, że królowa pozostawiła ślad stopy zaznaczony wapnem na kamieniu. Przyszła mu wtedy do głowy myśl, by odkuć ów ślad w kamieniu."
];

export const order = [
  "Według źródeł historycznych zakon karmelitański powstał w XII w., a dokładnej daty nie da się ustalić. Po zdobyciu Jerozolimy wielu przybyszów z Europy zaczęło napływać do Palestyny, a słysząc o świętości góry Karmel, niektórzy z nich: rycerze, pielgrzymi, pokutnicy, dołączali się do mieszkających tam pustelników, dając początki organizacji zakonu. Na początku XIII w. pustelnicy z Karmelu poprosili św. Alberta, łacińskiego patriarchę Jerozolimy, aby dał im regułę postępowania zgodną ze sposobem życia, który dotychczas prowadzili. Reguła, zwana też „formułą życia”, stała się zaczątkiem Reguły karmelitańskiej. Została zatwierdzona przez papieża Honoriusza III w 1226 r., a później, gdy zakon przeniósł się do Europy, adaptowana do nowych warunków przez papieża Innocentego IV w 1247 r. W XVI w. w wyniku reformy św. Teresy od Jezusa i św. Jana od Krzyża powstała druga gałąź karmelitów bosych.",
  "Hasło Zakonu Braci Najświętszej Maryi Panny z Góry Karmel nawiązuje do 1 Krl 19, 10: „Żarliwością rozpaliłem się o chwałę Boga Zastępów”."
];

export const founder = [
  "W pismach zakonu z końca XIII w. o założycielu wspomina się co następuje: „Ponieważ niektórzy młodzi bracia naszego zakonu nie wiedzą, jak zgodnie z prawdą odpowiedzieć tym, którzy pytają, przez kogo, kiedy i jak zaczął się nasz zakon, chcemy dać im pisemną odpowiedź w tym względzie.",
  "Orzekamy, dając świadectwo prawdzie, iż od czasów proroków Eliasza i Elizeusza, którzy pobożnie żyli na górze Karmel, święci ojcowie, czy to w Starym czy Nowym Testamencie, którzy byli miłośnikami samotności tejże góry dla kontemplacji niebiańskich rzeczy, żyli tam bez wątpienia chwalebnie w pobliżu źródła Eliasza w nieustannej świętej pokucie, przynosząc owoce uświęcenia. Ich następcy w czasach Innocentego III zebrali się w kolegium. Patriarcha Kościoła Jerozolimskiego Albert zatwierdził ich Regułę. Papież Honoriusz, następca Innocentego, jak i wielu ich następców zatwierdzili nasz zakon i potwierdzili go bullami. Poprzez naszą profesję my, ich naśladowcy, aż po dziś dzień służymy Panu na całym świecie”."
];

